import { connect } from "react-redux"
import Banner from "../Organisms/Banner"
import { useEffect } from "react"
import store from '../../Redux/store';
import {getAllPosts} from '../../Redux/actionCreators';
import Publication from "../Organisms/Publication";

import Courses from "./Courses";



const Home =({posts})=>{

  useEffect(() =>{
    store.dispatch(getAllPosts())
  },[])
  return(
    <>
    <Banner
    color="dark-color"
    image="https://edteam-media.s3.amazonaws.com/courses/small/461267d0-827f-4011-89ac-8403fe2eedb7.png"
    title="Bienvenido a la experiencia mas increible en educacion online"
    subtitle="Nuestro equipo ha desarrollado esta plataforma pensando en ti."
    home
    poster="https://edteam-media.s3.amazonaws.com/courses/small/4e8daacd-2485-4677-80c6-61aae551fe19.png"
    />

   




    
   
    <main className="ed-grid m-grid-3">
       <div className="l-block m-cols-2">
       <h2>
         Ultimas publicaciones
       </h2>
       {
         posts?
         <div>
         {
         posts.map(p=><Publication
            title={p.title}
            author={p.author}
            fecha={p.fecha}
            content={p.content}
            key={p.id}
          />)
       }
   
       </div>:
       <p>No existen publicaciones</p>
       }
       </div>

       <div>
         <h3>Lista de categorias</h3>
         <ul className="feature-list">
           <li><span>React.js</span></li>
           <li><span>React native</span></li>
           <li><span>Angular.js</span></li>
           <li><span>Vue.js</span></li>
           <li><span>HTML</span></li>
           <li><span>CSS</span></li>

         </ul>
       </div>
    </main>
    </>
  )
}
 


const mapStateToProps= state=>({
  posts:state.postReducer.posts
})

export default connect(mapStateToProps,{})( Home)
