import { useEffect } from "react"
import { connect } from "react-redux"
import { getCourse } from "../../Redux/actionCreators"
import store from "../../Redux/store"
import Banner from "../Organisms/Banner"




const Course =({course})=>{
  useEffect(()=>{
    store.dispatch(getCourse(1))
  },[])

  return(
 <>
  {
    course && <>
    <Banner
     color="dark-color"
     title={course.name}
     subtitle={course.subtitle}
     image={{
       src: "https://edteam-media.s3.amazonaws.com/courses/small/461267d0-827f-4011-89ac-8403fe2eedb7.png",
       alt: course.name
     }}
     courseBanner
     poster={course.picture}
     especialidad={course.data.specialities[0].name}
     info={course.information}
   />
   <main className="ed-grid lg-grid-10">
     <div className="lg-cols-7">

     
     <div className="course-fectures ed-grid lg-grid-3">
         <div>
           <h3 className="t4">¿Que aprenderas?</h3>
           <ul dangerouslySetInnerHTML={{__html: course.you_learn}}/>
         </div>

         <div>
           <h3 className="t4">Conocimientos previos</h3>
           <ul dangerouslySetInnerHTML={{__html: course.requirements}}/>
         </div>

         <div>
           <h3 className="t4">Nivel</h3>
           <p>{course.level}</p>
         </div>
     </div>
     <h2>Temario del curso</h2>
     <div className="s-border s-pxy-2 lg-pxy-4 s-radius s-bg-white l-block l-section">
       {
         course.data.classes.map(cl =>(
           <div className="course-class l-section" key={cl.class.id}>
             <h3>{cl.class.title}</h3>
             <p>{cl.class.description}</p>
             <ul>{
               cl.subjects.map(s=>(
                 <li>{s.subject.title}</li>
               ))
             }</ul>
           </div>
         ))
       }
     </div>
     </div>

     <div className="lg-cols-3">
       <h2 className="t3">Profesor</h2>
       <p>Robert Escalante</p>
     </div>
     
   </main>
    </>
  }
 </>

  )
}


const mapStateToProps=state=>({
  course: state.courseReducer.course
})

export default connect(mapStateToProps,{}) (Course)
