import { useEffect } from "react"
import { connect } from "react-redux"
import store from "../../Redux/store"
import { getAllCourses } from "../../Redux/actionCreators"
import Banner from "../Organisms/Banner"
import Card from "../Organisms/Card"

const Courses =({match,courses})=>{

  useEffect(()=>{
    store.dispatch(getAllCourses())
  },[match])

  return(

  <>
  <Banner
 color="dark-color"
     image={{
       src:"https://ed.team/images/utils/og-image.jpg",
       alt:"Banner cursos"
     }}
     title="Cursos"
     subtitle="Domina desde cero la tecnologia ,este es tu camino al exito"

  />
  {
    courses &&
    <main className="ed-grid m-grid-5">
      {
        courses.map(s=>(
         <Card
           path="cursos"
           picture={s.picture}
           name={s.name}
           key={s.id}
        
           cardId={s.id}
           
         />
        ))
      }
    </main>

  }


  </>
)}


const mapStateToProps= state =>({
  courses: state.courseReducer.courses
})

export default connect (mapStateToProps,{})(Courses)
