import { useEffect } from "react"
import { getAllTeachers } from "../../Redux/actionCreators"
import store from '../../Redux/store'
import Banner from "../Organisms/Banner"
import { connect } from "react-redux"
import Teacher from "../Organisms/Teacher"

const Teachers =({match,teachers})=>{

 useEffect(()=>{
   store.dispatch(getAllTeachers())
 },[match])


  return(
    <>
  <Banner
     color="third-color"
     image={{
       src:"https://ed.team/images/utils/og-image.jpg",
       alt:"Banner profesores"
     }}
     title="Nuestros profesores"
     subtitle="Este plantel docente esta altamente calificado para guiarte en tu futuro"

  />
     {
       teachers &&
  <main className="ed-grid m-grid-3 lg-grid-4 row-gap">
     {
       teachers.map(t=>(
         <Teacher
         key={t.id}
           picture={t.picture}
           name={t.name}
           country={t.country}
         />
       ))
     }
  </main>
     }
  </>
)}

const mapStateToProps= state =>({
  teachers: state.teacherReducer.teachers
})

export default connect (mapStateToProps,{})( Teachers)
