import { useEffect } from "react"
import { connect } from "react-redux"
import { getAllCourses } from "../../Redux/actionCreators"
import store from "../../Redux/store"
import Banner from "../Organisms/Banner"
import Card from "../Organisms/Card"

const Specialities =({match,specialities})=>{

  useEffect(()=>{
    store.dispatch(getAllCourses())
  },[match])

  return(
  <>
   <Banner
    color="first-color"
     image={{
       src:"https://ed.team/images/utils/og-image.jpg",
       alt:"Banner especialidades"
     }}
     title="Especialidades"
     subtitle="Domina una tecnologia con las rutas de aprendizajes que ofrecemos"


   />
   {
     specialities &&
     <main className="ed-grid m-grid-3 ">
     {
       specialities.map(s=>(
         <Card
         path="especialidades"
            picture={s.picture}
           name={s.name}
           key={s.id}
           cardId={s.id}
           path="especialidades"
         />
       ))
     }

     </main>
   }
   
   </>
)}

const mapStateToProps= state=>({
  specialities: state.specialityReducer.specialities
})

export default connect(mapStateToProps,{}) (Specialities)
